<template>
	<v-form v-model="valid" @submit.prevent="submit">
		<v-container>
			<v-col cols="12" md="12">
				<h2 class="main-header">Enter your data</h2>
			</v-col>
			<v-col cols="12" md="12">
				<v-text-field
					v-if="userType === 'user'"
					:disabled="loading"
					v-model="username"
					label="User name*"
					required
					type="username"
				></v-text-field>
				<v-select
					v-else
					:disabled="loading"
					:items="alegraSellers"
					v-model="alegraSeller"
					item-text="name"
					label="User name"
					:loading="loadingSellers"
				/>
			</v-col>

			<v-col cols="12" md="12">
				<v-select
					label="User type"
					:items="['user', 'seller']"
					:disabled="loading"
					v-model="userType"
				/>
			</v-col>
			<v-col cols="12" md="12">
				<v-text-field
					:disabled="loading"
					v-model="email"
					label="e-mail"
				></v-text-field>
			</v-col>

			<v-col cols="12" md="12">
				<v-text-field
					:disabled="loading"
					v-model="password"
					label="Password*"
					required
					type="password"
				></v-text-field>
			</v-col>

			<v-col cols="12" md="12">
				<v-text-field
					:disabled="loading"
					v-model="confirmPassword"
					label="Confirm password*"
					required
					type="password"
				></v-text-field>
			</v-col>

			<v-col cols="12" md="12">
				<v-btn
					class="mr-4"
					style="width: 100%"
					type="submit"
					:disabled="invalid"
				>
					<v-progress-circular
						v-if="loading"
						indeterminate
						color="primary"
						width="2"
					/>
					<div v-else>Sign-up</div>
				</v-btn>
			</v-col>

			<v-col cols="12" md="12">
				<v-row style="padding: 20px">
					<v-spacer />
					<div>
						Already have an account?...
						<router-link to="/login"> Login </router-link>
					</div>
				</v-row>
			</v-col>
		</v-container>
	</v-form>
</template>

<script>
import { mapActions } from "vuex";
import { createUser } from "../services/firebaseUserService";
import { getSellers } from "../services/alegraSellersService";
export default {
	data() {
		return {
			valid: false,
			username: "",
			email: "",
			password: "",
			confirmPassword: "",
			userType: "user",
			loading: false,
			loadingSellers: false,
			alegraSellers: null,
			alegraSeller: null,
		};
	},
	computed: {
		invalid() {
			return (
				this.loading ||
				!(
					this.valid &&
					this.username &&
					this.password &&
					this.confirmPassword == this.password
				)
			);
		},
	},
	mounted() {
		let type = this.$router.currentRoute.params.type;
		console.log({ type });
		if (type === "seller") this.userType = "seller";
	},
	methods: {
		...mapActions("common", ["setCurrentUser"]),
		async submit() {
			try {
				this.loading = true;
				let alegraInfo = {};
				if (this.userType === "seller")
					alegraInfo = this.alegraSellers.find(
						(s) => s.name === this.username
					);

				let usr = {
					password: this.password,
					type: this.userType,
					email: this.email,
					race: [],
					sellers: {},
					alegraInfo,
				};
				await createUser(this.username, usr);
				this.setCurrentUser({ ...usr, id: this.username });
				this.$router.push("#");
				this.$toast.success("Welcome");
			} catch (e) {
				this.$toast.error(e.message);
			} finally {
				this.loading = false;
			}
		},
	},
	watch: {
		async userType(val) {
			if (val === "seller") {
				try {
					this.loadingSellers = true;
					let sellers = await getSellers();
					this.alegraSellers = sellers;
					console.log(this.alegraSellers);
					let seller = null;
					if (this.username) {
						seller = sellers.find((i) => i.name === this.username);
						console.log("found", { seller });
					}
					if (!seller) {
						seller = sellers[0];
					}
					this.username = seller.name;
					this.alegraSeller = seller.name;
				} catch (e) {
					this.$toast.error(e.message);
					this.userType = "user";
				} finally {
					this.loadingSellers = false;
				}
			}
		},
		alegraSeller: {
			handler(val) {
				this.username = typeof val === "string" ? val : val.name;
			},
			deep: true,
		},
	},
};
</script>

<style></style>
